.te-header {
  position: absolute;
  top: 10px;
}
@media (min-width: 768px) {
  .te-header {
    top: 20px;
    left: 0;
  }
}
.te-header .te-header-logo {
  margin-left: 10px;
}
@media (min-width: 768px) {
  .te-header .te-header-logo {
    margin-left: 40px;
  }
}
.te-header .te-header-logo img {
  display: block;
  max-height: 30px;
  max-width: 120px;
}
@media (min-width: 768px) {
  .te-header .te-header-logo img {
    max-height: 50px;
    max-width: 140px;
  }
}
