.te-footer-container {
  font-family: inherit;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid #dcdee0;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .te-footer-container {
    font-size: inherit;
  }
}
.te-footer-terms {
  margin-bottom: 15px;
}
.te-footer-terms .te-terms-links .te-terms-link {
  display: inline-block;
  margin: 5px;
  cursor: pointer;
}
.te-footer-terms .te-terms-links .te-terms-link::after {
  content: '|';
  color: #444;
  display: inline-block;
  margin-left: 5px;
}
.te-footer-terms .te-terms-links .te-terms-link:last-of-type::after {
  content: '';
}
.te-footer-terms .te-terms-links .te-terms-link a {
  color: #b8a056;
  text-transform: none;
  text-decoration: none;
}
.te-footer-logo {
  margin: 15px;
}
@media (min-width: 768px) {
  .te-footer-logo {
    margin: 15px 20px;
  }
}
.te-footer-logo .te-footer-logo-img {
  width: 100px;
}
@media (min-width: 768px) {
  .te-footer-logo .te-footer-logo-img {
    width: 150px;
  }
}
.te-footer-logo .te-footer-logo-img img {
  display: block;
  width: 100%;
}
.te-footer .te-footer-payment-contact {
  margin-top: 10px;
}
.te-footer .te-footer-payment-contact .te-payment-link {
  margin: 5px;
}
.te-footer .te-footer-payment-contact .te-payment-link::after {
  content: '|';
  color: #444;
  display: inline-block;
  margin-left: 5px;
}
.te-footer .te-footer-payment-contact .te-payment-link:last-of-type::after {
  content: '';
}
.te-footer .te-footer-payment-contact .te-payment-link a {
  color: #b8a056;
  text-transform: none;
  text-decoration: none;
}
.te-footer-payment-logos {
  margin: 15px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.te-footer-payment-logos .te-payment-title {
  color: #474747;
  font-weight: bold;
}
.te-footer-payment-logos .te-payment-logos {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-payment-logos .te-payment-logos .te-payment-logo {
  width: 50px;
  height: 20px;
  padding: 14px;
  background: #000;
  margin-right: 15px;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .te-footer-payment-logos .te-payment-logos .te-payment-logo {
    width: 60px;
    height: 24px;
  }
}
